<template>
  <div>
    <v-breadcrumbs
      :items="items"
      divider="/"
    ></v-breadcrumbs>
    <v-data-table
      :search="search"
      :headers="headers"
      :items="researcharchives"
      item-key="full_name"
      class="table-rounded"
      :loading="loadingPages"
    >
      <template v-slot:top>
        <v-toolbar
          flat
        >
          <v-toolbar-title>Research Archives List</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            dark
            class="mb-2"
            @click="openModal()"
          >
            New
          </v-btn>
        </v-toolbar>
        <v-text-field
          v-model="search"
          label="Search..."
          class="mx-4"
        ></v-text-field>
      </template>
      <template #[`item.course`]="{item}">
        {{ `${item.course.course.toUpperCase()}` }}
      </template>
      <template #[`item.year_published`]="{item}">
        {{ `${item.year_from_published} - ${item.year_to_published}` }}
      </template>
      <template #[`item.adviser`]="{item}">
        <v-chip
          v-for="(adv, index) in item.adviser"
          :key="index"
          small
          class="font-weight-medium"
        >
          {{ adv.fullname }}
        </v-chip>
      </template>
      <template #[`item.panel`]="{item}">
        <v-chip
          v-for="(pan, index) in item.panel"
          :key="index"
          small
          class="font-weight-medium"
        >
          {{ pan.fullname }}
        </v-chip>
      </template>
      <template #[`item.member`]="{item}">
        <v-chip
          v-for="(mem, index) in item.member"
          :key="index"
          small
          class="font-weight-medium"
        >
          {{ mem.fullname }}
        </v-chip>
      </template>
      <template #[`item.actions`]="{item}">
        <v-icon
          small
          @click="selectUpdate(item)"
        >
          {{ icons.mdiBookEdit }}
        </v-icon>
      </template>
    </v-data-table>
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar
          dark
          color="primary"
        >
          <v-btn
            icon
            dark
            @click="dialog = false"
          >
            <v-icon>{{ icons.mdiClose }}</v-icon>
          </v-btn>
          <v-toolbar-title v-if="!isupdate">
            Create
          </v-toolbar-title>
          <v-toolbar-title v-if="isupdate">
            Update
          </v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>

        <v-card-text>
          <v-container>
            <v-row
              justify="center"
            >
              <v-col
                cols="12"
                sm="12"
                md="8"
              >
                <v-subheader>
                  <h3>Research Details</h3>
                </v-subheader>
                <v-row>
                  <v-col
                    cols="12"
                    sm="12"
                    md="12"
                  >
                    <v-textarea
                      v-model="request.title"
                      label="Research Title"
                    ></v-textarea>
                  </v-col>
                  <v-col
                    cols="12"
                    md="4"
                  >
                    <v-file-input
                      v-model="request.file"
                      accept="image/*"
                      label="File input"
                    ></v-file-input>
                  </v-col>
                  <v-col
                    cols="12"
                    md="4"
                  >
                    <v-select
                      v-model="request.course"
                      :items="courses"
                      item-text="course"
                      item-value="id"
                      label="Select Course"
                    ></v-select>
                  </v-col>
                  <v-col
                    cols="12"
                    md="4"
                  >
                    <v-text-field
                      v-model="request.tags"
                      label="Research Tags"
                      hint="Use comma (,) to separate tags."
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    md="4"
                  >
                    <v-menu
                      ref="menu_from"
                      v-model="menu_from"
                      :close-on-content-click="false"
                      :return-value.sync="request.year_from_published"
                      transition="scale-transition"
                      offset-y
                      max-width="290px"
                      min-width="auto"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                          v-model="request.year_from_published"
                          label="Year From Published"
                          prepend-icon="mdi-calendar"
                          readonly
                          v-bind="attrs"
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                        v-model="request.year_from_published"
                        type="month"
                        no-title
                        scrollable
                      >
                        <v-spacer></v-spacer>
                        <v-btn
                          text
                          color="primary"
                          @click="menu_from = false"
                        >
                          Cancel
                        </v-btn>
                        <v-btn
                          text
                          color="primary"
                          @click="$refs.menu_from.save(request.year_from_published)"
                        >
                          OK
                        </v-btn>
                      </v-date-picker>
                    </v-menu>
                  </v-col>
                  <v-col
                    cols="12"
                    md="4"
                  >
                    <v-menu
                      ref="menu_to"
                      v-model="menu_to"
                      :close-on-content-click="false"
                      :return-value.sync="request.year_to_published"
                      transition="scale-transition"
                      offset-y
                      max-width="290px"
                      min-width="auto"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                          v-model="request.year_to_published"
                          label="Year To Published"
                          prepend-icon="mdi-calendar"
                          readonly
                          v-bind="attrs"
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                        v-model="request.year_to_published"
                        type="month"
                        no-title
                        scrollable
                      >
                        <v-spacer></v-spacer>
                        <v-btn
                          text
                          color="primary"
                          @click="menu_to = false"
                        >
                          Cancel
                        </v-btn>
                        <v-btn
                          text
                          color="primary"
                          @click="$refs.menu_to.save(request.year_to_published)"
                        >
                          OK
                        </v-btn>
                      </v-date-picker>
                    </v-menu>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
            <v-divider></v-divider>
            <v-row
              justify="center"
            >
              <v-col
                cols="12"
                sm="12"
                md="4"
              >
                <v-subheader class="text-center">
                  <h3>Adviser(s)</h3>
                </v-subheader>
                <v-text-field
                  v-model="adviser"
                  outlined
                  label="Enter to add Adviser"
                  @keyup.enter="adviserAdd()"
                ></v-text-field>
                <v-list-item
                  v-for="(adv, index) in request.advisers"
                  :key="index"
                >
                  <v-list-item-title>{{ adv.fullname }}</v-list-item-title>
                  <v-list-item-icon>
                    <v-icon @click="adviserRemove(index)">
                      {{ icons.mdiTrashCan }}
                    </v-icon>
                  </v-list-item-icon>
                </v-list-item>
              </v-col>
              <v-col
                cols="12"
                sm="12"
                md="4"
              >
                <v-subheader>
                  <h3>Panels</h3>
                </v-subheader>
                <v-text-field
                  v-model="panel"
                  outlined
                  label="Enter to add Panel"
                  @keyup.enter="panelAdd()"
                ></v-text-field>
                <v-list>
                  <v-list-item
                    v-for="(pan, index) in request.panels"
                    :key="index"
                  >
                    <v-list-item-content>
                      <v-list-item-title v-text="pan.fullname"></v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-icon>
                      <v-icon @click="panelRemove(index)">
                        {{ icons.mdiTrashCan }}
                      </v-icon>
                    </v-list-item-icon>
                  </v-list-item>
                </v-list>
              </v-col>
              <v-col
                cols="12"
                sm="12"
                md="4"
              >
                <v-subheader>
                  <h3>Members</h3>
                </v-subheader>
                <v-text-field
                  v-model="member"
                  outlined
                  label="Enter to add Panel"
                  @keyup.enter="memberAdd()"
                ></v-text-field>
                <v-list>
                  <v-list-item
                    v-for="(mem, index) in request.members"
                    :key="index"
                  >
                    <v-list-item-content>
                      <v-list-item-title v-text="mem.fullname"></v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-icon>
                      <v-icon @click="memberRemove(index)">
                        {{ icons.mdiTrashCan }}
                      </v-icon>
                    </v-list-item-icon>
                  </v-list-item>
                </v-list>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="closeModal()"
          >
            Cancel
          </v-btn>
          <v-btn
            v-if="!isupdate"
            color="primary"
            :loading="loading"
            :disabled="loading"
            @click="create()"
          >
            Save
          </v-btn>
          <v-btn
            v-if="isupdate"
            color="primary"
            :loading="loading"
            :disabled="loading"
            @click="update()"
          >
            Update
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import sweetalert2 from '@/plugins/sweetalert2'
import {
  mdiSquareEditOutline, mdiDotsVertical, mdiBookEdit, mdiClose, mdiTrashCan,
} from '@mdi/js'
import {
  mapGetters,
  mapActions,
} from 'vuex'

export default {
  data() {
    return {
      menu_from: false,
      menu_to: false,

      isupdate: false,
      dialog: false,
      loadingPages: true,
      loading: false,
      formTitle: 'Create',
      items: [
        {
          text: 'Settings',
          disabled: true,
          href: '#',
        },
        {
          text: 'Research Archives',
          disabled: false,
          href: '#',
        },
      ],
      search: '',
      headers: [
        { text: 'CODE', value: 'code' },
        { text: 'RESEARCH TITLE', value: 'title' },
        { text: 'COURSE', value: 'course' },
        { text: 'TAGS', value: 'tags' },
        { text: 'YEAR PUBLISHED', value: 'year_published' },
        { text: 'ADVISERS', value: 'adviser' },
        { text: 'PANELS', value: 'panel' },
        { text: 'MEMBERS', value: 'member' },
        { text: 'ACTIONS', value: 'actions', sortable: false },
      ],

      // icons
      icons: {
        mdiSquareEditOutline,
        mdiDotsVertical,
        mdiBookEdit,
        mdiClose,
        mdiTrashCan,
      },

      adviser: null,
      panel: null,
      member: null,

      // Input field
      request: {
        id: null,
        title: '',
        course: '',
        file: '',
        tags: '',
        year_from_published: '',
        year_to_published: '',
        advisers: [],
        panels: [],
        members: [],
      },
    }
  },
  computed: {
    ...mapGetters(['courses', 'researcharchives']),
  },
  mounted() {
    this.getCourses()
    this.getResearcharchives().then(() => {
      this.loadingPages = false
    })
  },
  methods: {
    // eslint-disable-next-line no-unused-vars
    openModal() {
      this.dialog = true
      this.isupdate = false
      this.request = {
        id: null,
        title: '',
        course: '',
        file: null,
        tags: '',
        year_from_published: '',
        year_to_published: '',
        advisers: [],
        panels: [],
        members: [],
      }
    },
    adviserAdd() {
      if (this.adviser == null) {
        sweetalert2('warning', 'Adviser field empty!')

        return
      }
      this.request.advisers.push({
        fullname: this.adviser,
      })
      this.adviser = null

      sweetalert2('success', 'Adviser added!')
    },
    adviserRemove(index) {
      this.request.advisers.splice(index, 1)

      sweetalert2('success', 'Adviser removed!')
    },
    panelAdd() {
      if (this.panel == null) {
        sweetalert2('warning', 'Panel field empty!')

        return
      }
      this.request.panels.push({
        fullname: this.panel,
      })
      this.panel = null

      sweetalert2('success', 'Panel added!')
    },
    panelRemove(index) {
      this.request.panels.splice(index, 1)
      sweetalert2('success', 'Panel removed!')
    },
    memberAdd() {
      if (this.member == null) {
        sweetalert2('warning', 'Member field empty!')

        return
      }
      this.request.members.push({
        fullname: this.member,
      })
      this.member = null

      sweetalert2('success', 'Member added!')
    },
    memberRemove(index) {
      this.request.members.splice(index, 1)
      sweetalert2('success', 'Member removed!')
    },
    create() {
      this.loading = true
      const formdata = new FormData()
      formdata.append('title', this.request.title)
      formdata.append('course', this.request.course)
      formdata.append('file', this.request.file)
      formdata.append('tags', this.request.tags)
      formdata.append('year_from_published', this.request.year_from_published)
      formdata.append('year_to_published', this.request.year_to_published)
      formdata.append('advisers', JSON.stringify(this.request.advisers))
      formdata.append('panels', JSON.stringify(this.request.panels))
      formdata.append('members', JSON.stringify(this.request.members))

      this.createResearcharchives(formdata).then(res => {
        this.loading = false
        if (res.data.status === 200) {
          sweetalert2('success', res.data.message)
          this.closeModal()
        } else {
          sweetalert2('warning', res.data.message)
        }
      }).catch(() => {
        sweetalert2('warning', 'Server error!')
      })
    },
    update() {
      this.loading = true
      this.updateResearcharchives(this.request).then(res => {
        this.loading = false
        if (res.request.status === 200) {
          sweetalert2('success', res.request.message)
          this.closeModal()
        } else {
          sweetalert2('warning', res.request.message)
        }
      }).catch(() => {
        sweetalert2('warning', 'Server error!')
      })
    },
    selectUpdate(item) {
      this.request = {
        id: item.id,
        title: item.title,
        course: item.course_id,
        file: null,
        tags: item.tags,
        year_from_published: item.year_from_published,
        year_to_published: item.year_to_published,
        advisers: item.adviser,
        panels: item.panel,
        members: item.member,
      }
      this.dialog = true
      this.isupdate = true
    },
    closeModal() {
      this.dialog = false
      this.isupdate = false
      this.request = {
        id: null,
        title: '',
        course: '',
        file: null,
        tags: '',
        year_from_published: '',
        year_to_published: '',
        advisers: [],
        panels: [],
        members: [],
      }
    },
    ...mapActions(['getCourses', 'getResearcharchives', 'createResearcharchives', 'updateResearcharchives']),
  },
}
</script>
